      title: "<<Ապրելուց քաղցր է մեռնել քեզ համար, զգալ, որ դու կաս և լինել հեռու, երկրպագել քեզ առանց սիրվելու>>, <<Ինչ որ չկարողացար, քեզ կներվի, բայց ինչը որ չուզեցիր՝ երբեք>>, <<Իր արժեքն իմացող մարդը երբեք ավելին չի անի, քան ինչին ինքն արժանի է>>, <<Որպեսզի կյանքն ընթանա այնպես, ինչպես ժամացույցը, այն երբեմն պետք է լարել>>, <<Լեզուն ազգի հոգին է. կենդանի է այդ հոգին, կենդանի է ազգը, կենսունակ է առաջինը, ուրեմն՝ կենսունակ է նաև երկրորդը>>, <<Մարդու ամենաթանկարժեք սեփականությունը անկեղծությունն է>>, <<Փառքը հաճախ մարդու արժանիքի ցուցանիշը չէ>>, <<Կան տաղանդի տեր մարդիկ՝ օժտված ընդունակություններով դեպի գիտությունը, պոեզիան, արվեստը ընդհանրապես, կան մարդիկ էլ՝ մարդկայնության տաղանդի տեր։ Ես ուզում եմ ասել մոտավորապես, որ Պուշկինը տաղանդավոր բանաստեղծ է, իսկ X-ը՝ տաղանդավոր մարդ>>, <<Կարելի է լինել հանճարեղ գրող, կոմպոզիտոր, մաթեմատիկոս և ապաշնորհ մարդ։ Կարելի է գիտության և արվեստի մի բնագավառում չունենալ ընդունակություններ, բայց լինել հանճարեղ մարդ>>, <<Չէ՞ որ մարդիկ երկուսով միշտ ավելի մոտ, ինտիմ և անկեղծ են, քան երբ նրանք երեքով են՝ անկախ հարաբերություններից>>",
      birbth: "1885",
      death: "1920"
