      title: "<<Ա՜խ, իմ մեծ Սերը…Նա հավասար է Կորած հույսերիս ճիշտ քառակուսուն Ու երազանքիս խեղճ մնացորդին>>, <<Չէ, ասեղ չէ սերը, որ թաքցնես, Իսկ թե ասեղ է նա՝ Աչք է ծակում>>, <<Շա՜տ կուզեի, Որ նա սիրել կարողանար Նաև…տգեղ տղամարդու…>>, <<Կա մի աշխարհ մեր աշխարհում՝ Ինձ հարազատ, ձեզ անծանոթ>>, <<Կան թանկ հուշեր, որ սիրո պես ուռճանում են լու’ռ, աննկա’տ, Եվ բացվում են միանգամից` ինչպես վարդը մի գիշերում>>, <<Սեղմե՜մ մի կոճակ՝ Եվ մարդկանց հոգում Ծաղիկների պես լույսերը ծաղկեն…>>, <<Ու թե մութ մնա, Մնա ի’մ կամքով, Ու մնա միայն իբրև վարագույր Համբույրի համար սիրահարների>>, <<Երազել է տալիս մութը, Իսկ երազել մենք չենք ուզում>>, <<Ջանում եմ կտրել մի պատառ աշուն. Ա’ռ, որ ձմռանը ցրտում չտխրես>>, <<Առողջ աչքով ես տեսնում եմ, Իսկ կույր աչքով…մի՜շտ երազում…>>",
