      description1: "Հայաստան (Բացել ֆայլի մասին տվյալները արտասանություն), պաշտոնական անվանումը՝ Հայաստանի Հանրապետություն, պետություն Առաջավոր Ասիայում[7]՝ Հայկական լեռնաշխարհի հյուսիսարևելյան մասում։ Քաղաքական և մշակութային իմաստով, սակայն, գտնվում է հարավարևելյան Եվրոպայի Կովկասյան տարածաշրջանում։ Հյուսիսում սահմանակցում է Վրաստանին, արևելքում՝ Ադրբեջանին, հարավում՝ Իրանին, իսկ արևմուտքում՝ Թուրքիային։ Հարավարևելյան կողմում Բերձորի միջանցքով կապվում է Արցախի Հանրապետությանը, իսկ հարավ-արևմուտքում Ադրբեջանի էքսկլավ Նախիջևանի Ինքնավար Հանրապետությունն է։ Այժմյան Հայաստանը զբաղեցնում է պատմական Հայաստանի տարածքի միայն մեկ տասներորդը՝ Այրարատ և Սյունիք նահանգների մի մասը[8]։ «Հայաստան» անվանումը վերաբերում է նաև ողջ Հայկական լեռնաշխարհին, որտեղ կազմավորվել և իր պատմական ուղին է անցել հայ ժողովուրդը։ Ըստ հայոց պատմահայր Մովսես Խորենացու՝ հայ ժողովրդի պատմության սկիզբն է համարվում մ.թ.ա. 2492 թվականը, երբ հայ ժողովրդի անվանադիր նախահայրը՝ Հայկ նահապետը, Հայոց ձորի ճակատամարտում հաղթում է Ասորեստանի բռնարար տիրակալ Բելին և անկախություն նվաճում[4]։ Հայալեզու ցեղերը Հայկական լեռնաշխարհի հարավարևմտյան շրջաններում ապրել են մ.թ.ա. 2-րդ հազարամյակի վերջերից։ Արամե արքայի ջանքերով մ․թ․ա․ 860 թվականին հիմնադրվում է Վանի թագավորությունը, որի գոյության ժամանակահատվածում էլ՝ մ․թ․ա․ 782 թվականին, Արգիշտի Ա թագավորը հիմնադրում է Հայաստանի մերօրյա մայրաքաղաք Երևանը։ Մ․թ․ա․ 1-ին դարում՝ Տիգրան Բ Մեծի կառավարման տարիներին, Մեծ Հայքի Արտաշեսյան թագավորությունը հասնում է իր հզորության գագաթնակետին՝ ձգվելով Կասպից ծովից մինչև Միջերկրական ծով, Կովկասյան լեռներից մինչև Միջագետքի անապատները։",
