      dascription: "Պետրա (արաբ․՝ البتراء, հին քաղաք, Եդոմի մայրաքաղաքը, ավելի ուշ՝ Նաբաթեայի մայրաքաղաքը[1]։ Գտնվում է ժամանակակից Հորդանանի տարածքում՝ ծովի մակարդակից 900 մ և շրջակայքից 660 մ բարձրության վրա՝ Սիկ անունով նեղ կանիոնում։Դեպի հարթավայր ելքն անցնում է հյուսիսում և հարավում ընկած նեղ կիրճերով, մինչդեռ արևելքում և արևմուտքում ժայռերը միանգամից կտրատվում են՝ առաջացնելով 60 մ բարձրությամբ բնական պատեր։ Պետրայից ոչ հեռու գտնվում են Ադ-դեյր տաճարը և Ահարոնի դամբարանը։2005 թվականին Պետրան ընդգրկվել է ՅՈՒՆԵՍԿՕ-ի Համաշխարհային ժառանգության ցանկում[2]։ՊատմությունՊետրան ընկած է առևտրական երկու կարևոր ճանապարհների խաչմերուկում, որոնցից մեկը միացնում էր Կարմիր ծովը Դամասկոսին, մյուսը Միջերկրական ծովի մոտ Պարսից ծոցը կապում է Ղազային։ Պարսից ծոցից ուղևորվող քարավանները՝ բարձված թանկարժեք համեմունքներով, ստիպված էին շաբաթներ շարունակ հերոսաբար դիմակայել Արաբական անապատի դաժան պայմաններին, մինչ կհասնեին բավական նեղ Սիկ կանիոնի զովությանը. այնտեղից մոտ էր երկար սպասված Պետրան։ Այնտեղ ճանապարհորդները գտնում էին կերակուր, ծածկ և սառը, կենարար ջուր։Հարյուրավոր տարիներ առևտուրը Պետրային մեծ հարստություն է բերել։ Սակայն երբ հռոմեացիները բացահայտեցին արևելք տանող ծովային ճանապարհը, համեմունքների առևտրի ցամաքային առևտուրը անկում ապրեց, և Պետրան աստիճանաբար դատարկվեց՝ կորչելով ավազների մեջ։ Քաղաքի արաբերեն անվանումն է Ալ-Բատրա,իսկ հունարեն՝ Պետրա, որը նշանակում է《քար》:Պետրայի շատ շինություններ կառուցվել են տարբեր դարաշրջաններում և տարբեր տիրակալների օրոք, այդ թվում՝ իդումեացիների (XVIII-II դարեր մ.թ.ա.), նաբաթեացիների (II դար մ.թ.ա. - 106 թվական մ.թ.), հռոմեացիների (106-395 թվականներ մ.թ.), բյուզանդացիների և արաբների։ 12-րդ դարում այն տիրել են խաչակիրները։Ժամանակակից եվրոպացիներից առաջինը Պետրան տեսել և նկարագրել է շվեյցարացի ճանապարհորդ Յոհան Լյուդվիգ Բուրկհարդը։",
